import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ToastController } from 'ionic-angular';
import { ConcertsProvider } from '../../providers/concerts/concerts';

/**
 * Generated class for the EditUserPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-edit-user',
  templateUrl: 'edit-user.html',
})
export class EditUserPage {
  tilgjengeligebilletter: {};
  user: any;
  concerts: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private alertCtrl: AlertController,
    private concertService: ConcertsProvider,
    private toastCtrl: ToastController
  ) {
    this.user = navParams.get('user');
    console.log(this.user);
    this.makeUserConcerts();
  }

  makeUserConcerts() {
    this.concertService.getConcerts().then(concerts => {
      this.concerts = concerts;
      // console.log(this.concerts);

      for (let concert of this.concerts) {
        this.concertService.getNumberOfAvailableTickets(concert).then(n => {
          concert.availableTickets = n;
        });

        concert.number = 0;
        for (let ticket_id of this.user.concerts) {
          if (ticket_id == concert._id) {
            concert.number += 1;
          }
        }
      }
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EditUserPage');
  }

  saveChanges() {
    //remove all concerts
    this.user.concerts = [];
    // Update this user so that it has corresponding concert-tickets in user.concerts
    for (let concert of this.concerts) {
      for (var i = 0; i < concert.number; i++) {
        console.log(concert.title + ':' + concert.number);
        this.user.concerts.push(concert._id);
      }
    }
    console.log(this.user);
    this.concertService.updateUser(this.user._id, this.user).then(res=>{
      this.presentToast();
    })
  }

  presentToast() {
    let toast = this.toastCtrl.create({
      message: 'Bruker oppdatert',
      duration: 3000
    });
    toast.present();
  }

}



